import React, { useState, useEffect } from 'react'
import { Row, Col, Button, Space, message, } from 'antd';
import Api from '../../helpers/api';

import {
    SaveOutlined,
    ArrowLeftOutlined,
    ReloadOutlined,
} from '@ant-design/icons';
import TableMaterialVendor from './TableMaterialVendor';
import TableMaterialRoom from './TableMaterialRoom';
import handleApiError from '../../helpers/handleApiError';

const api = new Api();

const authobjectCfg = {
    // approve: 'delivery-order-approve',
    // reject: 'delivery-order-reject',
}

const FormMaterial = (props) => {

    const [errors, setErrors] = useState({})
    const [loading, setLoading] = useState(false)
    const [selectedMaterial, setSelectedMaterial] = useState([])
    const [quantity, setQuantity] = useState([])
    const [price, setPrice] = useState([])
    const [total, setTotal] = useState(0)
    const [deliveryQuantity, setDeliveryQuantity] = useState([])
    const [deliverySerial, setDeliverySerial] = useState([])

    const { formData, view, process } = props

    useEffect(() => {
        if (formData.details) {
            setSelectedMaterial(formData.details)
            setTotal(parseInt(formData.total_price))
        }
    }, [formData])

    const handleChangeQuantity = (value) => {
        setQuantity(value)
    }

    const handleSubmit = () => {
        selectedMaterial.map((item, index) => {
            let indexDeliveryQty = deliveryQuantity.map(deliveryQty => deliveryQty.id).lastIndexOf(item.id)
            selectedMaterial[index]['delivery_quantity'] = deliveryQuantity[indexDeliveryQty] && deliveryQuantity[indexDeliveryQty].id == item.id
                ? deliveryQuantity[indexDeliveryQty].val
                : 0
        })

        let data = { materials: selectedMaterial, serials: deliverySerial }

        const { id } = props.match.params

        let apiPost = api.processDeliveryOrder(id, data)

        setLoading(true)
        apiPost.then(response => {
            console.log(response)
            setLoading(false)
            message.success('save data success');
            props.history.goBack();
        }).catch(error => {
            setLoading(false)
            if (error.response && error.response.data) {
                const { errors } = error.response.data
                setErrors(errors)
            }
            // handleApiError(error, props)
        });
    }

    const handleChangeDeliveryQuantity = (value) => {
        setDeliveryQuantity(value)
    }

    const handleChangeDeliverySerial = (value) => {
        setDeliverySerial(value)
    }

    const handleReset = () => {
        setQuantity([])
    }

    return (
        <Space direction="vertical" size="large" style={{ width: '100%' }}>
            <Row>
                <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                    {errors &&
                        <div className="ant-form-item-explain ant-form-item-explain-error">
                            <div role="alert">
                                {Object.values(errors)}
                            </div>
                        </div>}

                    <h2>Total Price: {`${total}`.replace(/\B(?=(\d{3})+(?!\d))/g, '.')}</h2>
                </Col>
            </Row>
            <Row>
                <Col xs={24} sm={24} md={24} lg={24} xl={24}>

                    {formData.type === 0
                        ?
                        <TableMaterialVendor
                            {...props}
                            selectedMaterial={selectedMaterial}
                            handleChangeQuantity={handleChangeQuantity}
                            deliveryQuantity={handleChangeDeliveryQuantity}
                            deliverySerial={handleChangeDeliverySerial}
                            selectedMaterialDelete={[]} />
                        :
                        <TableMaterialRoom
                            {...props}
                            selectedMaterial={selectedMaterial}
                            handleChangeQuantity={handleChangeQuantity}
                            selectedMaterialDelete={[]} />
                    }
                </Col>
            </Row>

            <Space>
                <Button onClick={() => props.history.goBack()}
                    icon={<ArrowLeftOutlined />}
                >Back</Button>

                {view ??
                    <>
                        <Button
                            onClick={() => handleReset()}
                            icon={<ReloadOutlined />}
                        >Reset</Button>

                        <Button type="primary" htmlType="submit"
                            onClick={handleSubmit}
                            loading={loading}
                            icon={<SaveOutlined />}
                        >Save</Button>
                    </>
                }
            </Space>
        </Space>
    )
}

export default FormMaterial;